import React from 'react';
import PropTypes from 'prop-types';
import withStyles from 'isomorphic-style-loader/lib/withStyles';
import s from './Assessment.css';

import httpRequest from '../../components/Helper/httpRequest';


class Assessment extends React.Component {
	constructor(props){
		super(props);
		this.loadJson = this.loadJson.bind(this);
		this.state = {
            data: [],
        };
	}
	
  static propTypes = {
    title: PropTypes.string.isRequired,
  };
  
  loadJson=(arg, event)=>{
	var d = httpRequest.get('/assessment/page_2.js').then(res =>{
		console.log(res.ExternalData[0].randomizationOption);
		console.log(res.ExternalData[0].Modules[0].objectives[0].questions);
		this.setState({data: res.ExternalData[0].Modules[0].objectives[0].questions})
	});
	
  }

  render() {
    return (
      <div className={s.root}>
        <div className={s.container}>
          <h1>{this.props.title}</h1>
		  
          <div id="stage" ref="stage" className={s.stage}>
			<button onClick={(e)=>this.loadJson(1, e)} >Start</button> 
			{this.state.data.map(item => (
				<article key={item.qusid} className={s.newsItem}>
				  <h3 className={s.newsTitle}>
					<a href={item.link}>{item.question}</a>
				  </h3>
				  <div>
					{item.description}
				  </div>
				  <div
					className={s.newsDesc}
					// eslint-disable-next-line react/no-danger
					dangerouslySetInnerHTML={{ __html: item.content }}
				  />
				</article>
			  ))}
		  
		  </div>
        </div>
      </div>
    );
  }
}

export default withStyles(s)(Assessment);
